<template>
  <table class="pdf-table fs-12 w-100 pdf-table-freight mt-2 text-center" :class="`fs-${fs_type}-18`">
    <thead>
      <tr class="">
        <th width="10%">項號</th>
        <th width="15%">貨號</th>
        <th width="40%">描述</th>
        <th width="20%">數量</th>
        <th width="15%">單位</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="(f, index) in _combineList" :key="f.id">
        <td>{{ index + 1 }}</td>
        <td>{{ f.num }}</td>
        <td>{{ f.named }}</td>
        <td>{{ f.count }}</td>
        <td>{{ unit(f.unit) }}</td>
      </tr>
    </tbody>
  </table>
</template>

<script>
import view from '@/common/es/view'
import tool from '@/common/es/tool'

export default {
  name: 'PdfCombineBuy',
  props: {
    _combineList: {
      type: Array
    },
    fs_type: {
      type: String,
      default: 'em'
    }
  },
  methods: {
    unit(u) {
      return view.ser_unit(u)
    },
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/pdf.sass"
</style>


