<template>
  <print-pdf-simple-layout>

    <pdf-header slot="header">訂貨單</pdf-header>

    <pdf-combine-msg slot="msg"
      :_listingNumList="combineNums"

      v-if="combineNums.length > 0"
    ></pdf-combine-msg>

    <pdf-combine-buy 
      :_combineList="combineList" 
      v-if="combineList.length > 0"

      :fs_type="`vw`"
      slot="freights"
    ></pdf-combine-buy>

  </print-pdf-simple-layout>
</template>

<script>
import conn from '@/service/conn'
import tool from '@/common/es/tool'
import view from '@/common/es/view'
import config from '@/common/config'

import PrintPdfSimpleLayout from '@/components/Pdf/Print/PrintPdfSimpleLayout.vue'
import PdfCombineBuy from '@/components/Pdf/Combine/PdfCombineBuy.vue'
import PdfCombineMsg from '@/components/Pdf/Combine/PdfCombineMsg.vue'
import PdfHeader from '@/components/Pdf/PdfHeader.vue'

export default {
  components: { PrintPdfSimpleLayout, PdfCombineBuy, PdfCombineMsg, PdfHeader },
  name: 'PrintCombineListingPdf',
  data() {
    return {
      combineList: [],
      combineNums: [],
      allowPrint: false
    }
  },
  created() {
    let res = tool.storage._get('combine_listing_content_list')
    if ((res != null) && (res != '') && (res != undefined)) {
      this.combineList = JSON.parse(res)
      if (this.combineList.length > 0) {
        this.allowPrint = true
      }
    } 


    res = tool.storage._get('combine_listing_num_list')
    if ((res != null) && (res != '') && (res != undefined)) {
      this.combineNums = JSON.parse(res)
    } 

  },
  mounted() {
    if (this.allowPrint) {
      print()
    }
  },
  methods: {

  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/pdf.sass"

</style>


