<template>
  
  <table class="pdf-table w-100 fs-12" :class="`fs-${fs_type}-18`">
    <tbody>
      <tr>
        <td class="pt-3 pl-4">
          含發票單編號：&nbsp;&nbsp;{{ numString }}
        </td>
      </tr>
      <tr>
        <td class="pb-3 pt-3 pl-4">
          創建時間：&nbsp;&nbsp;{{ createTimed }}
        </td>
      </tr>
    </tbody>
  </table>

</template>

<script>
import tool from '@/common/es/tool'

export default {
  name: 'PdfCombineMsg',
  props: {
    _listingNumList: {
      type: Array,
      default: null
    },
    fs_type: {
      type: String,
      default: 'vw'
    }
  },
  data() {
    return {
      createTimed: ''
    }
  },
  computed: {
    numString() {
      if (this._listingNumList == null) { return '' }
      return this._listingNumList.join('、')
    }
  },
  created() {
    this.createTimed = tool.timed.getNowDate('yyyy') + '年' + tool.timed.getNowDate('mm') + '月' + tool.timed.getNowDate('dd') + '日'
  },
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/pdf.sass"

</style>


